@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  font-size: 62.5%;
}
.banner_container{
    width: -webkit-fit-available;
    background-color: black;
    -webkit-clip-path: polygon(0 0 , 100% 0, 100% 79%, 82% 100%, 0 100%);
            clip-path: polygon(0 0 , 100% 0, 100% 79%, 82% 100%, 0 100%);
}

.banner_container .banner{
    width: 96%;
    margin: 0px auto;
}

.banner_container .banner .image_slider{
    width: 100%;
    min-width: 100%;
    /* display: flex;
    overflow: hidden; */
}

.image1{
    width: 100%;
    -webkit-clip-path: polygon(60% 0, 100% 0, 100% 60%, 86% 100%, 26% 100%);
            clip-path: polygon(60% 0, 100% 0, 100% 60%, 86% 100%, 26% 100%);
}

.image2 , .image3 , .image4{
    width: 100%;
    -webkit-clip-path: polygon(14% 0, 100% 0, 100% 60%, 86% 100%, 0 100%, 0 40%);
            clip-path: polygon(14% 0, 100% 0, 100% 60%, 86% 100%, 0 100%, 0 40%);
}

.name{
    padding: 70px;
    
}

.name .welcome{
    margin-left: 6vw;
    color: white;
    text-decoration: underline;
    text-transform: uppercase;
    font-family: 'Roboto', sans-serif;
    font-weight: bolder;
    font-size: 1.9rem;
}

.name .welcome_desc{
    margin-left: 6vw;
    color: white;
   margin-top: 20px;
   font-size: 1.2rem;
}

.image{
    position: relative;
}

.slider_content{
    position: absolute;
    top: 0;
    left: 0;
    color: black;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0px 100px;
}

.white_text{
    color: white;
}

.slider_content .inner_content{
    font-size: 4.75rem;
    font-weight: 800;
    margin-bottom: 1.7rem;
    line-height: 0.95;
    text-transform: uppercase;
    font-family: 'Roboto', sans-serif;
}

.banner_btn{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    border: 4px solid black;
    padding: 0px 4px;
    font-size: 1.1rem;
    font-weight: 700;
    cursor: pointer;
    color: black;
    background: transparent;
    font-family: 'Roboto', sans-serif;
}

.banner_btn svg{
    margin-left: 16px;
}

.white_border{
    border: 4px solid white;
    color: white;
}

.white_border::after{
    content: ".";
    width: 60px;
    height: 100%;
    background-color: red;
    color: transparent;
    margin: 0px 8px;
    -webkit-clip-path: polygon(0 0 , 0 100% , 100% 50%);
            clip-path: polygon(0 0 , 0 100% , 100% 50%);
}

.carousel-indicators{
    display: block;
    left: 0;
    margin: 15px;
}

.carousel-indicators button.active{
    background-color: black;
}

.changing button{
    position: static;
    position: initial;
    display: inline;
    width: inherit;
    margin: 0px 10px;
}

.changing{
    position: absolute;
    bottom: 0;
    right: 0;

}

@media (max-width : 1120px) {
    .slider_content{
        position: static;
        position: initial;
    }
    .slider_content .inner_content{
        color: white;
        font-size: 2rem;
    }
    .banner_btn{
        border: 4px solid white;
        color: white;
    }
}

.main_container{
    position: relative;
    --initial :polygon(100% 0, 100% 0, 100% 100%, 0 100%, 0 28%);
    transition: all .4s;
}

.main_container::before{
    content: ".";
    background-color: black;
    width: 100%;
    height: 100%;
    position: absolute;
    -webkit-clip-path: var(--initial);
            clip-path: var(--initial);
}

.main_container .featured_container{
    padding: 100px 4vw;
}

.main_container .featured_container .featured .heading{
    font-size: 1.6rem;
    color: black;
    font-weight: 600;
    text-decoration: underline;
    margin-bottom: 30px;
}

.articles{
    display: grid;
    grid-template-columns: repeat(auto-fit , minmax(300px , calc(50% - 5px)));
    justify-content: center;
    grid-gap: 10px;
}

.articles .card{
    color: white;
    background: black;
    padding: 20px;
    font-size: 1.3rem;
    overflow: hidden;
    position: relative;
}

.articles .card .line{
    height: 8px;
    width: 100%;
    background: white;
    margin: 10px 0px;
    z-index: 10;
}

.articles .card .cardhead{
    padding: 0px 30px;
    position: relative;
}

.articles .card .cardhead::before{
    content: ".";
    color: transparent;
    background-color: #FF7F2F;
    width: 30px;
    height: 78%;
    position: absolute;
    left: -5px;
    top: 8px;
    -webkit-clip-path: polygon(0 0 , 0 100% , 100% 0);
            clip-path: polygon(0 0 , 0 100% , 100% 0);
}

.articles .card .cardhead .card_heading{
    font-weight: 700;
}

.articles .card .cardhead .card_subheading{
    font-weight: 500;
}

.articles .card .card-body .main_text{
    font-weight: 600;
}

.articles .card .card-body .main_desc{
    font-size: 1.1rem;
}

.card .card-img-top{
    transition: all .3s;
}

.card .card-img-top:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.card_btn{
    text-align: right;
    cursor: pointer;
}

.card_btn span{
    margin: 0px 10px;
    cursor: pointer;
}

.card_btn span svg{
    color: #FF7F2F;
    font-size: 1rem;
    cursor: pointer;
}

.play_btn{
    position: absolute;
    border: 4px solid white;
    padding: 0px 14px;
    font-size: 1.1rem;
    font-weight: bold;
    cursor: pointer;
}

@media (max-width : 700px){
    .articles{
        grid-template-columns: repeat(auto-fit , minmax(300px , 90%));
    }
}

.nav1{
    display: flex;
}

.header{
    justify-content: center;
    padding: 30px;
}

.inner_header{
    margin: 0px 2vw;
    justify-content: space-between;
    width: 100%;
}

a{
    text-decoration: none;
    color: black;
}

.right_head .navigation .menu_list{
    list-style: none;
    display: grid;
    width: 650px;
    font-size: 1.4rem;
    grid-template-columns: repeat(2,1fr) repeat(2 , 1.5fr);
    place-items: center;
    margin-right: 30px;
    font-family: 'Roboto', sans-serif;
    text-transform: uppercase;
}

.right_head .navigation .menu_list .items{
    text-align: center;
    position: relative;
}

.triangle{
    position: relative;
}

.triangle::before{
    content: ".";
    width: 100px;
    background-color: red;
    position: absolute;
    color: transparent;
    -webkit-clip-path: polygon(0 0, 15% 0, 0 40%);
            clip-path: polygon(0 0, 15% 0, 0 40%);
    left: -16px;
    top: 4px;
    display: none;
}

.items:hover .triangle{
    text-decoration: underline;
    color: black;
}

.items:hover .triangle::before{
   display: block;
}

.search_btn svg{
    font-size: 2.1rem;
}

.menubtn{
    margin-left: 50px;
}

.menubtn svg{
    font-size: 2.4rem;
 }

.search_btn:hover svg{
    color: rgba(250, 165, 8, 0.87);
}
.menubtn:hover svg{
    color: rgba(250, 165, 8, 0.87);
}

@media (max-width : 1120px) {
    .right_head .navigation .menu_list{
        display: none;
    }
}
